/* stylelint-disable */
body {
  font-family: "Roboto", sans-serif;
  height: 100%;
}

.site-container {
  height: 100%;
}

.main {
  height: 100%;
}

.container {
  max-width: 500px;
  padding: 0px 15px;
  margin: 0 auto;
}

.stores {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #f9fafb;
}
.stores__title {
  font-size: 20px;
  margin-bottom: 20px;
}
.stores__wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  box-shadow: 0 3px 6px 0 rgba(36, 52, 63, 0.1);
}
.stores__block {
  width: 100%;
  padding: 20px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px #f3f6f9 solid;
  cursor: pointer;
}
.stores__block:hover {
  background-color: #f9fafb;
}
.stores__info {
  display: flex;
  align-items: center;
}
.stores__icon {
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: indigo;
  border-radius: 50%;
  color: #fff;
  margin-right: 20px;
}
.stores__letter {
  font-size: 25px;
  text-transform: uppercase;
}
.stores__name {
  font-size: 15px;
  font-weight: bold;
}
.stores__count {
  margin-top: 5px;
  font-size: 12px;
  color: rgb(102, 118, 128);
}
.stores__coupon {
  display: flex;
  background-color: #f2f5f9;
  padding: 10px 20px 10px 20px;
  font-size: 16px;
  text-transform: uppercase;
  border-radius: 15px;
  transition: all 0.3s ease;
  align-items: center;
  cursor: pointer;
}
.stores__coupon svg {
  width: 12px;
  height: 12px;
  margin-right: 5px;
}
.stores__coupon:hover {
  background-color: #24343f;
  color: #fff;
}
.stores__coupon:hover svg {
  fill: #fff;
}

.details {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #f9fafb;
  height: 100vh;
}
.details__title {
  font-size: 20px;
}
.details__wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  box-shadow: 0 3px 6px 0 rgba(36, 52, 63, 0.1);
}
.details__block {
  width: 100%;
  padding: 20px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px #f3f6f9 solid;
}
.details__name {
  font-size: 14px;
  color: #667680;
  margin-right: 20px;
}
.details__nav {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
.details__shop-name {
  font-size: 15px;
}
.details__shop-name span {
  font-weight: bold;
}

.coupon {
  display: flex;
  background-color: #f2f5f9;
  padding: 10px 20px 10px 20px;
  font-size: 14px;
  text-transform: uppercase;
  border-radius: 15px;
  transition: all 0.3s ease;
  align-items: center;
  cursor: pointer;
}
.coupon svg {
  width: 12px;
  height: 12px;
  margin-right: 5px;
}
.coupon:hover {
  background-color: #24343f;
