}

.back {
  display: flex;
  padding: 10px 20px;
  background-color: transparent;
  border: 1px solid #d5d3d4;
  border-radius: 10px;
  margin-bottom: 20px;
  transition: all 0.3s ease;
}
.back:hover {
  background-color: #24343f;
  color: #fff;
}

.dnone {
  display: none;
}