.details {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #f9fafb;
  height: 100vh;
  &__title {
    font-size: 20px;
  }
  &__wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    box-shadow: 0 3px 6px 0 rgb(36 52 63 / 10%);
  }
  &__block {
    width: 100%;
    padding: 20px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px #f3f6f9 solid;
  }

  &__name {
    font-size: 14px;
    color: #667680;
    margin-right: 20px;
  }

  &__nav {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }

  &__shop-name {
    font-size: 15px;
    span {
      font-weight: bold;
    }
  }
}
