// базовые подключения
@import "./vendor/normalize";
@import "./settings";
@import "vars";
@import "mixins";
@import "fonts";

body {
  font-family: "Roboto", sans-serif;
  height: 100%;
}

.site-container {
  height: 100%;
}

.main {
  height: 100%;
}

.container {
  max-width: 500px;
  padding: 0px 15px;
  margin: 0 auto;
}

// подключения компонентов страницы
@import "./components/header";
@import "./components/stores";
@import "./components/details";

.coupon {
  display: flex;
  background-color: #f2f5f9;
  padding: 10px 20px 10px 20px;
  font-size: 14px;
  text-transform: uppercase;
  border-radius: 15px;
  transition: all 0.3s ease;
  align-items: center;
  cursor: pointer;
  svg {
    width: 12px;
    height: 12px;
    margin-right: 5px;
  }
  &:hover {
    background-color: #24343f;
    color: #fff;
    svg {
      fill: #fff;
    }
  }
}

.back {
  display: flex;
  padding: 10px 20px;
  background-color: transparent;
  border: 1px solid #d5d3d4;
  border-radius: 10px;
  margin-bottom: 20px;
  transition: all 0.3s ease;
  &:hover {
    background-color: #24343f;
    color: #fff;
  }
}

.dnone {
  display: none;
}

@import "./media";
