.stores {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #f9fafb;
  &__title {
    font-size: 20px;
    margin-bottom: 20px;
  }
  &__wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    box-shadow: 0 3px 6px 0 rgb(36 52 63 / 10%);
  }
  &__block {
    width: 100%;
    padding: 20px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px #f3f6f9 solid;
    cursor: pointer;
    &:hover {
      background-color: #f9fafb;
    }
  }
  &__info {
    display: flex;
    align-items: center;
  }
  &__icon {
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: indigo;
    border-radius: 50%;
    color: #fff;
    margin-right: 20px;
  }
  &__letter {
    font-size: 25px;
    text-transform: uppercase;
  }
  &__name {
    font-size: 15px;
    font-weight: bold;
  }
  &__count {
    margin-top: 5px;
    font-size: 12px;
    color: rgb(102, 118, 128);
  }
  &__coupon {
    display: flex;
    background-color: #f2f5f9;
    padding: 10px 20px 10px 20px;
    font-size: 16px;
    text-transform: uppercase;
    border-radius: 15px;
    transition: all 0.3s ease;
    align-items: center;
    cursor: pointer;
    svg {
      width: 12px;
      height: 12px;
      margin-right: 5px;
    }
    &:hover {
      background-color: #24343f;
      color: #fff;
      svg {
        fill: #fff;
      }
    }
  }
}
